<template>
  <div class="search-result">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text=""
      @load="onLoad"
      :error.sync="error"
        error-text=""
    >
      <van-cell
        v-for="(item, index) in list"
        :key="index"
        :title="item.title"

      />
    </van-list>
  </div>
</template>

<script>
import { searchResult } from '@/api/result'
export default {
  name: 'SearchResult',
  components: {},
  props: {
    searchText: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1,
      perPage: 20,
      error: false
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    async onLoad () {
      try {
        const { data } = await searchResult({
          page: this.page,
          per_page: this.perPage,
          q: this.searchText
        })

        // 
        // if (Math.random() > 0.5) {
        //   JSON.parse('aaaaa')
        // }

        const { results } = data.data
        this.list.push(...results)

        this.loading = false
        if (results.length) {
          this.page++
        } else {
          this.finished = true
        }
      } catch (err) {
        this.error = true
        this.loading = false
      }
    }
  }
}
</script>

<style scoped lang="less"></style>
